import { mapGetters } from 'vuex'
import { addArticles } from '@/api/article'
export default {
  // 在路由匹配组件渲染之前会先执行中间件处理
  middleware: 'authenticated',
  name: 'edit',
  data() {
    return {
      article: {
        title: '',
        description: '',
        body: '',
        tagList: []
      },
      tagInput: null,
      errors: {},
      inProgress: false
    }
  },
  computed: {
    ...mapGetters(['user'])
  },
  methods: {
    async onPublish() {
      this.inProgress = true
      const { data } = await addArticles({ article: this.article })
      this.$router.push({ path: `article/${data.article.slug }`})
      this.inProgress = false
    },
    addTag(tag) {
      this.article.tagList.push(tag)
      this.tagInput = null
    },
    removeTag(tag) {
      this.article.tagList.filter((item, index) => {
        if (item === tag) {
          this.article.tagList.splice(index)
          return true
        }
      })
    }
  }
}
